@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.navbar
{
background-color: blanchedalmond;
}
.navbar-brand
{
font-size: 1.9rem;
color: #565387;
}

.navbar .nav-link {
    font-size: 1.3rem; 
    text-transform: capitalize; 
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid  #565387;
}
.navbar .nav-link:hover{
    
    transform: scale(1.1);
}

@media(max-width:991px){
    #header{
        height: 80vh;
        text-align: center;
    }
    #header .header-img{
        text-align: center;
    }
    #header .header-img img{
        width: 60%;
    }

    .navbar-brand{
        font-size: 1.5rem;
    }
    .menu_active{
        font-weight: bold;
        border-bottom: none;
    }
}
@media(max-width:768px){
#header{
margin-top: 20px;
}
#header h1{
    font-size: 18px;
    margin-bottom: 30px;
}
#header .header-img img{
    width: 70%;
}
}

#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

#header h1{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}


#header .brand-name{
    color: #3498db;
}

#header h2{
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-get-started:hover{
    background: #3498db;
    color: #fff;
}
.btn_con:hover{
    background: #3498db;
    color: #fff;
}
.btn_con{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}
.header-img{
    text-align: right;
}
.card-text{
    
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.card:hover
{ /* IE 9 */ /* Safari 3-8 */
  transform: scale(1.1);
  
}
.card{
    transition: 0.5s;
}

.card-body .btn {
    background: #3498db;
}
.card-body .card-title{
    margin: 0 0 10px 0;
    font-weight: 700;
    color: black;
}

#header .animated {
    animation: mover 2s infinite  alternate;
}
@keyframes mover {
    0% { transform: translateY(30); }
    100% { transform: translateY(-30px); }
}
